.container {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 600px;
    height: 450px;
    margin: 10px auto;
    
  }
  
  .current-image {
    border-radius: 0.5rem;
    box-shadow: 0px 0px 7px #666;
    width: 100%;
    height: 100%;
   
  }
  
  .arrow {
    position: absolute;
    width: 2rem;
    height: 2rem;
    color: #57e752;
    filter: drop-shadow(0px 0px 5px #555);
    cursor: pointer;
  }
  .arrow:hover{
    width: 2.1rem;
    height: 2.1rem;
  }
  
  .arrow-left {
    left: 1rem;
  }
  
  .arrow-right {
    right: 1rem;
  }
  
  .circle-indicators {
    display: flex;
    position: absolute;
    bottom: 1rem;
  }
  
  .current-indicator {
    background-color: #ffffff;
    height: 5px;
    width: 25px;
    border: none;
    outline: none;
    margin: 0 0.2rem;
    cursor: pointer;

  }
  
  
  .hide-current-image{
      display: none;
  }
  
  .inactive-indicator{
      background-color: gray;
      width: 5px;
  }